import React, { useState } from 'react';
import { register as apiRegister } from '../../services/auth';
import { useAuth } from '../../context';
import { useToast } from '../../context/ToastContext';

export function RegisterForm({ onSuccess }: { onSuccess?: () => void }) {
  const { login } = useAuth();
  const { showToast } = useToast();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [username, setUsername] = useState('');
  const [fullName, setFullName] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const validate = () => {
    const errors: string[] = [];
    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) errors.push(' email');
    if (password.length < 8) errors.push(':  8 ');
    if (!/[A-Z]/.test(password)) errors.push(':     ');
    if (!/[a-z]/.test(password)) errors.push(':     ');
    if (!/\d/.test(password)) errors.push(':    ');
    if (username) {
      if (username.length < 3) errors.push(' :  3 ');
      if (username.length > 50) errors.push(' :   50 ');
      if (!/^[a-zA-Z0-9_-]+$/.test(username)) errors.push(' :   , , -  _');
    }
    if (fullName && fullName.length > 255) errors.push(' :   255 ');
    return errors;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    const validationErrors = validate();
    if (validationErrors.length) {
      setError(validationErrors.join('\n'));
      return;
    }
    setLoading(true);
    try {
      await apiRegister({ email, password, username, full_name: fullName });
      showToast(' ', 'success');
      //    
      try {
        await login(email, password);
        showToast(' ', 'success');
      } catch (_) {
        //          
        showToast('   ', 'info');
      }
      onSuccess?.();
    } catch (err: any) {
      setError(err?.message || ' ');
      showToast(err?.message || ' ', 'error');
    } finally {
      setLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} style={{ display: 'grid', gap: 8, maxWidth: 320 }}>
      <h3></h3>
      <input type="email" placeholder="Email" value={email} onChange={(e) => setEmail(e.target.value)} required />
      <input type="password" placeholder="" value={password} onChange={(e) => setPassword(e.target.value)} required />
      <input type="text" placeholder=" " value={username} onChange={(e) => setUsername(e.target.value)} />
      <input type="text" placeholder=" " value={fullName} onChange={(e) => setFullName(e.target.value)} />
      {error && <pre style={{ whiteSpace: 'pre-wrap', color: 'red', fontSize: 12 }}>{error}</pre>}
      <button type="submit" disabled={loading}>{loading ? '...' : ''}</button>
      <div style={{ fontSize: 12, color: '#666' }}>
        :  8 ,    ,    .    //"-"/"_".
      </div>
    </form>
  );
}

